.app-container {
  max-width: 90%;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.store-container {
  max-width: 100%;
  display: flex;
  padding: 10px auto;
  justify-content: space-between;
  border:1px solid silver;
}


  .items-container {
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: 10px auto;
    padding: 10px auto;
    justify-content: space-between;
  }

  
    .basket-container {
     min-width:150px;
     max-width:300px;
      margin: 0;
      padding: 0 20px;
      text-align: center; /* Центрирование контента по горизонтали */
      position: relative; /* Добавляем позиционирование для .basket */
    }
    .basket-container p {
      margin-top: 150px; /* Увеличиваем отступ снизу для параграфов */
    }
    
  
  
  .item {
    border: 1px solid #ccc;
    margin-top:0;
    padding:  0 10px;
    flex-basis: calc(45% - 10px);
    margin-bottom: 10px;
  }
.item img {
    width: 200px;
    height: auto;
    object-fit: contain;
    margin-bottom: 10px;
    transition: transform 0.3s ease-in-out;
  }

  .item img.expanded {
    width: 250px;
    height: auto;
    position: 'absolute';
    top: 'calc(100% + 10px)';
    left: 0;
    z-index: 9999;
  }

  .item img.hovered {
    cursor: pointer;
    transform: scale(1.2);
  }
  .basket {
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    color: #fff;
    background-image: url(../img/basket.png);
    background-size: cover;
    background-position: center center;
    width: 100px;
    height:110px;
    padding: 5px 10px;
    border-radius: 10px;
    position: absolute; /* Позиционируем .basket абсолютно внутри .basket-container */
  left: 50%; /* Располагаем .basket по горизонтали на 50% относительно .basket-container */
  transform: translate(-50%); /* Сдвигаем .basket на половину его размера влево, чтобы центрировать его по центру .basket-container */
  }
  
 
  .basket span {
    margin-left: 50px;
  }
  button {
  background-color: #af824c; /* Green */
  border: none;
  color: white;
  padding: 5px 10px;
  text-align: center;
  text-decoration: none;

  font-size: 14px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 20px;
}

button:hover {
  background-color: #3e8e41;
}

button:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.category-select {
  display: flex;
  justify-content: center;
  margin: 20px 10px;
  padding:20px o;
}
#category-select{
margin:20px 10px;
}

.category-select button {
  margin: 10px;
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.category-select button:hover {
  background-color: #0062cc;
}

.category-select button.active {
  background-color: #0062cc;
}
ul li {
  text-align: left;
  color: black;
  margin: 0 auto;
  list-style-type: square;
  margin-left:0px;
  }
/* Стили для мобильных устройств */
@media (max-width: 934px) {
  .app-container {
    padding: 10px; /* Уменьшаем внутренние отступы */
  }
  .store-container {
    flex-direction: column; /* Переключаем расположение на столбец */
    align-items: center; /* Центрируем элементы по горизонтали */
    padding: 5px; /* Уменьшаем внутренние отступы */
  }
  .basket {
    width: 80px; /* Уменьшаем ширину корзины */
    height: 90px; /* Уменьшаем высоту корзины */
    font-size: 1rem; /* Уменьшаем размер шрифта */
  }
  .basket span {
    margin-left: 40px; /* Уменьшаем отступ */
  }
  .category-select {
    flex-direction: column; /* Переключаем расположение на столбец */
    align-items: center; /* Центрируем элементы по горизонтали */
    margin: 10px 0; /* Уменьшаем внешние отступы */
    padding: 0; /* Уменьшаем внутренние отступы */
  }
  #category-select {
    margin: 10px 0; /* Уменьшаем внешние отступы */
  }
  .category-select button {
    margin: 5px; /* Уменьшаем отступы между кнопками */
    padding: 6px 12px; /* Уменьшаем внутренние отступы */
    font-size: 12px; /* Уменьшаем размер шрифта */
  }
  
}
@media (min-width: 935px) and (max-width: 1020px) {
  
  .basket-container {
    max-width: 200px; /* Уменьшаем ширину контейнера */
    margin: 0 auto; /* Смещаем контейнер вправо */
  }
  .items-container {
    max-width: calc(100% - 220px); /* Уменьшаем ширину .items-container на ширину .basket-container и небольшой отступ */
  }
  .item {
    flex-basis: calc(33.33% - 8px); /* Устанавливаем ширину элемента на 1/3 ширины контейнера и учитываем отступы */
    margin-bottom: 10px; /* Уменьшаем пространство между элементами */
  }
}






  